import { selector } from "recoil";
import { historyAtom } from "../atoms/GameState";
import { putterCountAtom, rollingAverageDepthAtom } from "../atoms/GameConfig";

export const graphSelector = selector({
  key: "graph",
  get: ({ get }) => {
    const history = get(historyAtom);
    const rollingAverageDepth = get(rollingAverageDepthAtom);
    const putterCount = get(putterCountAtom);

    const adjustedAvgDept = Math.ceil(rollingAverageDepth / putterCount);

    const graphData = [];

    for (let i = 0; i < history.length; i++) {
      const start = Math.max(0, i - adjustedAvgDept + 1);
      const localHistory = history.slice(start, i + 1);

      let nominator = 0;
      let denominator = 0;

      localHistory.forEach((value, index) => {
        const weight = (index + 1) / (localHistory.length + 1);
        const percentage = value / putterCount;
        nominator += percentage * weight;
        denominator += weight;
      });

      graphData.push(nominator / denominator);
    }

    return graphData;
  },
});
