import React from "react";
import styled from "styled-components";
import { useRecoilState, useSetRecoilState } from "recoil";
import { putterCountAtom } from "../../../recoil/atoms/GameConfig";
import { historyAtom } from "../../../recoil/atoms/GameState";

const StyledDiv = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding: 4px;
  max-height: 50vh;
  overflow: auto;

  button {
    font-family: "Staatliches", sans-serif;
    height: 80px;
    flex: 1;
    min-width: calc(33.333% - 8px);
    margin: 4px;
    font-size: 40px;
    font-weight: bold;
    border-radius: 4px;
    background: radial-gradient(
        80% 80% at 50% 50%,
        rgba(255, 255, 255, 0.08) 0%,
        rgba(0, 0, 0, 0) 100%
      ),
      #1e1e21;
    color: #fcf3e2;
    border-style: outset;
    border-width: 8px;
    text-shadow: 0 0 12px black, 1px 1px 0 #8d847e;
    border-color: #44444b;

    &:active {
      border-width: 12px;
      background: radial-gradient(
          79.35% 79.35% at 50% 50%,
          rgba(255, 255, 255, 0.04) 0%,
          rgba(0, 0, 0, 0) 100%
        ),
        #151518;
    }

    transition: border-width 0.25s cubic-bezier(0.33, 1, 0.68, 1),
      background 0.25s cubic-bezier(0.33, 1, 0.68, 1);
  }
`;

export default function NumberPad() {
  const [putterCount] = useRecoilState(putterCountAtom);
  const setHistory = useSetRecoilState(historyAtom);

  return (
    <StyledDiv>
      {[...Array(putterCount + 1).keys()].map((i) => (
        <button
          key={i}
          onClick={() => {
            setHistory((history) => [...history, i]);
          }}
        >
          {i}
        </button>
      ))}
    </StyledDiv>
  );
}
