import React from "react";
import styled from "styled-components";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { healthSelector } from "../../../recoil/selectors/Health";
import { progressSelector } from "../../../recoil/selectors/Progress";
import { progressGoalAtom } from "../../../recoil/atoms/GameConfig";
import { ReactComponent as DialBackgroundSVG } from "../../../assets/images/DialBackground.svg";
import { ReactComponent as ClearButtonSVG } from "../../../assets/images/ClearButton.svg";
import { ReactComponent as SettingsButton } from "../../../assets/images/SettingsButton.svg";
import { historyAtom } from "../../../recoil/atoms/GameState";

interface StyledDivProps {
  $healthNeedleRotation: number;
  $progressNeedleRotation: number;
}

const StyledDiv = styled.div<StyledDivProps>`
  position: relative;

  .dial-border {
    width: calc(100% + 4px);
    left: -2px;
    aspect-ratio: 2 / 1;
    border-top-left-radius: 100% 195%;
    border-top-right-radius: 100% 195%;
    position: absolute;
    top: -204px;
    background-color: #381b1b;
    display: block;
    filter: none;
    box-shadow: 0 0 32px rgba(0, 0, 0, 0.4);
  }

  .corner-filler {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: -168px;
    background-color: #381b1b;
    box-shadow: 0 0 32px rgba(0, 0, 0, 0.4);
  }

  .clear-button {
    position: absolute;
    right: 7px;
    top: -160px;
  }

  .settings-button {
    position: absolute;
    left: 7px;
    top: -160px;
  }

  .dial-background {
    width: calc(100% - 8px);
    position: absolute;
    bottom: -75px;
    left: 4px;
    pointer-events: none;
  }

  .needle {
    position: absolute;
    left: calc(50% - 4px);
    bottom: 8px;
    width: 8px;
    height: 8px;
    filter: drop-shadow(0 0 8px rgba(0, 0, 0, 0.5));
    transition: transform 0.5s;

    svg {
      position: absolute;
      left: -11px;
      top: -12px;
    }
  }

  .health-needle {
    transform: scale(0.38) rotate(${(props) => props.$healthNeedleRotation}deg);
  }

  .goal-needle {
    transform: scale(0.18)
      rotate(${(props) => props.$progressNeedleRotation}deg);
  }
`;

export default function Dial() {
  const health = useRecoilValue(healthSelector);
  const progress = useRecoilValue(progressSelector);
  const progressGoal = useRecoilValue(progressGoalAtom);
  const setHistory = useSetRecoilState(historyAtom);

  const healthNeedleRotation = -90 + health * 90;
  const progressNeedleRotation =
    -180 + Math.min(progress / progressGoal, 1) * 180;

  return (
    <StyledDiv
      $healthNeedleRotation={healthNeedleRotation}
      $progressNeedleRotation={progressNeedleRotation}
    >
      <div className="dial-border" />
      <div className="corner-filler" />

      <div className="dial-background">
        <DialBackgroundSVG className="dial-background" />
      </div>

      <SettingsButton className="settings-button" />

      <ClearButtonSVG
        className="clear-button"
        onClick={() => {
          setHistory((oldHistory) =>
            oldHistory.slice(0, oldHistory.length - 1)
          );
        }}
      />

      <div className="needle health-needle">
        <svg
          width="454"
          height="31"
          viewBox="0 0 454 31"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M453 14C453 14 25.5762 0 16 0C6.42375 0 0 6.5 0 15C0 23.5 7.49997 30.5 16 30.5L453 16C453 16 454 16 454 15C454 14 453 14 453 14Z"
            fill="#FF0000"
          />
        </svg>
      </div>
      <div className="needle goal-needle">
        <svg
          width="454"
          height="31"
          viewBox="0 0 454 31"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M453 14C453 14 25.5762 0 16 0C6.42375 0 0 6.5 0 15C0 23.5 7.49997 30.5 16 30.5L453 16C453 16 454 16 454 15C454 14 453 14 453 14Z"
            fill="#BB0000"
          />
        </svg>
      </div>
    </StyledDiv>
  );
}
