import { selector } from "recoil";
import { graphSelector } from "./Graph";
import { targetAtom } from "../atoms/GameConfig";

const HEALTH_CHANGE = 0.1;

export const healthSelector = selector({
  key: "health",
  get: ({ get }) => {
    const graph = get(graphSelector);
    const target = get(targetAtom);

    let health = 0;

    for (let i = 0; i < graph.length; i++) {
      if (health <= -0.99 || health >= 0.99) {
        health = 0;
        continue;
      }

      if (graph[i] < target) {
        health -= HEALTH_CHANGE;
      } else {
        health += HEALTH_CHANGE;
      }
    }

    return health;
  },
});
