import React, { Fragment } from "react";
import styled from "styled-components";
import { useRecoilValue } from "recoil";
import { graphSelector } from "../../../recoil/selectors/Graph";
import {
  graphDepthAtom,
  putterCountAtom,
  targetAtom,
} from "../../../recoil/atoms/GameConfig";
import { historyAtom } from "../../../recoil/atoms/GameState";

const StyledDiv = styled.div`
  flex: 1;
  margin: 0 8px;
  background-color: #e3d0bc;
  border-radius: 4px;
  padding: 8px 0;
`;

const GRAPH_PADDING = 4;

export default function Graph() {
  const data = useRecoilValue(graphSelector);
  const target = useRecoilValue(targetAtom);
  const history = useRecoilValue(historyAtom);
  const putterCount = useRecoilValue(putterCountAtom);
  const graphDepth = useRecoilValue(graphDepthAtom);

  const width = window.screen.width - 16;
  const height = 300;

  function getXCoord(index: number): string {
    return ((width / graphDepth) * index).toFixed(1);
  }

  function getYCoord(value: number): string {
    return (GRAPH_PADDING + height - value * height).toFixed(1);
  }

  function getCoord(value: number, index: number): string {
    return `${getXCoord(index)},${getYCoord(value)}`;
  }

  const targetY = getYCoord(target);

  const points = data
    .slice(-graphDepth)
    .map((value, index) => getCoord(value, index))
    .join(" ");

  return (
    <StyledDiv>
      <svg width={width} height={height + GRAPH_PADDING * 2}>
        <line
          x1={0}
          y1={targetY}
          x2={width}
          y2={targetY}
          stroke="gray"
          strokeDasharray="12 8"
          strokeWidth="3"
        />
        <polyline points={points} fill="none" stroke="black" strokeWidth="3" />
        {history.slice(-graphDepth).map((value, index) => (
          <Fragment key={`trade-dot-${index}`}>
            <line
              key={`trace-${index}`}
              x1={getXCoord(index)}
              y1={getYCoord(value / putterCount)}
              x2={getXCoord(index)}
              y2={getYCoord(
                data[data.length - Math.min(graphDepth, history.length) + index]
              )}
              stroke="gray"
              strokeDasharray="8"
            />
            <circle
              key={`dot-${index}`}
              cx={getXCoord(index)}
              cy={getYCoord(value / putterCount)}
              fill="gray"
              r="3"
            />
          </Fragment>
        ))}
      </svg>
    </StyledDiv>
  );
}
